<template>
  <div class="product_list">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">home</el-breadcrumb-item>
        <el-breadcrumb-item>注册机构</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="product_main">
      <div class="explain">
        注册机构分类说明：1.未在国内上市销售的从植物、动物、矿物等物质中提取的有效成份及其制剂。2.新发现的药材及其制剂。3.新的中药材代用品。4.药材新的药用部位及其制剂。5.未在国内上市销售的从植物、动物、矿物等物质中提取的有效部位及其制剂。6.未在国内上市销售的中药、天然药物复方制剂。7.改变国内已上市销售中药、天然药物给药途径的制剂。
      </div>
      <div class="list" v-for="(item, i) of 6" :key="i">
        <div class="list_title">国家生物医学实验细胞资源库</div>
        <div class="list_content">
          国家标本资源共享平台（NSII）是国家科技部认定并资助的28个国家科技基础条件平台之一，汇集了植物、动物、岩矿化石和极地标本数字化信息的在线共享平台，自2003年开始建设。
        </div>
      </div>
      <div class="paginaton">
        <el-pagination
          :background="false"
          :pager-count="9"
          layout="prev, pager, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :total="80"
        >
        </el-pagination>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
      this._getSearch();
      this.loading = true;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this._getSearch();
      this.loading = true;
    },
  },
  created() {},
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f9f9f9;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 40px 24px 0;
  .explain {
    font-size: 14px;
    color: #595959;
    line-height: 24px;
    width: 1152px;
    margin-bottom: 24px;
  }
  .list {
    width: 1152px;
    border-bottom: 1px solid #e8e8e8;
    margin-top: 18px;
    .list_title {
      font-size: 16px;
      color: #0695d4;
      line-height: 24px;
    }
    .list_content {
      font-size: 14px;
      color: #595959;
      line-height: 24px;
      margin: 12px 0 16px;
    }
  }
}
</style>



